import {classQues} from '../common/newDB'
import BScroll from 'better-scroll'
const TITLE_HEIGHT = 48
export default {
    // 父组件传递的数据
    props:["data","count"],
    data(){
        return {
            // answered: 0,
            // unanswered: 0,
            // answertrue: 0,
            // answerfalse: 0,
            isShowAnsCard: false,
            currentIndex: 0,
            titleHeight: 0,
            diff: 0,
            latterArr: ["A","B","C","D","E","F"]
        }
    },
    // 计算属性
    computed:{
        classifyData() {
            return classQues(this.data);
        },
        fixedTitle() {
            if (this.scrollY > 0) {
              return ''
            }
            return this.classifyData[this.currentIndex] ? this.classifyData[this.currentIndex].title : ''
        },
        answertrue() {
            var num = 0;
            this.data.forEach((item,index)=>{
                if(item.result) {
                    num++
                }
            });
            return num;
        },
        answerfalse() {
            var num = 0;
            this.data.forEach((item,index)=>{
                if(item.result == false) {
                    num++
                }
            });
            return num;
        },
        unanswered() {
            return this.count-(this.answertrue+this.answerfalse)
        }
    },
    created() {
        // this.listHeight = []
    },
    mounted(){

    },
    watch: {
        // isShowAnsCard(val) {
        //     if(val) {
        //         setTimeout(() => {
        //             this._calculateHeight();
        //         }, 20)
        //     }
        // },
        diff(val) {
            let fixedTop = (val > 0 && val < TITLE_HEIGHT) ? val - TITLE_HEIGHT : 0
            if (this.fixedTop === fixedTop) {
              return
            }
            this.fixedTop = fixedTop;
            if(this.$refs.fixed) {
                this.$refs.fixed.style.transform = `translate3d(0,${fixedTop}px,0)`
            }

        }
        // data() {
        //     setTimeout(() => {
        //         this._calculateHeight();
        //     }, 20)
        // }
    },
    methods: {
        // scrollY(e) {
        //     const listHeight = this.listHeight
        //     // 当滚动到顶部，newY>0
        //     if (e.target.scrollTop  == 0) {
        //       this.currentIndex = 0
        //       return
        //     }
        //     // 在中间部分滚动
        //     for (let i = 0; i < listHeight.length - 1; i++) {
        //       let height1 = listHeight[i]
        //       let height2 = listHeight[i + 1]
        //       if (e.target.scrollTop >= height1 && e.target.scrollTop < height2) {
        //         this.currentIndex = i
        //         this.diff = height2 - e.target.scrollTop
        //         return
        //       }
        //     }
        //     // 当滚动到底部，且-newY大于最后一个元素的上限
        //     this.currentIndex = listHeight.length - 2
        // },
        // _calculateHeight() {
        //     this.listHeight = []
        //     const list = this.$refs.listGroup
        //     let height = 0
        //     // this.$refs.ansheader.offsetHeight
        //     this.listHeight.push(height)
        //     for (let i = 0; i < list.length; i++) {
        //       let item = list[i]
        //       height += item.clientHeight
        //       this.listHeight.push(height)
        //     }
        // },
        answer(item) {
            var latter = [];
            item.options.map((item,index)=> {
                if(item.isAnswer) {
                     latter.push(this.latterArr[index]);
                }
            })
            return latter;
        },
        open() {
            this.isShowAnsCard = true;
            // this.data.map((item,index)=>{
            //     if(item.checked && this.answer(item).indexOf(item.checked)>=0){
            //         // item.result = "true"
            //         this.answertrue++
            //     } else if(item.checked && this.answer(item).indexOf(item.checked)<0) {
            //         // item.result = "false"
            //         this.answerfalse++
            //     }
            // });
            // this.unanswered = this.count-(this.answertrue+this.answerfalse)
        },
        close(){
            this.isShowAnsCard = false;
            // setTimeout(()=> {
            //     this.answertrue = 0;
            //     this.answerfalse = 0;
            // },300)
        },
        jump(index){
            this.$emit("jump",index);
            this.close();
        }
    }
}



