
import {mapMutations, mapState} from 'vuex'
import {getQuestionType, getTopQuestion, getQuestion, favorite, delFavorite,saveLearnLocation, saveAnswerHistory,getLearnLocation} from '../api/request'
import {filterData} from '../common/newDB'
import Answercard from "./answercard"
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Loading from 'vux/src/components/loading/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import Toast from 'vux/src/components/toast/index.vue'

import Vue from 'vue'
Vue.use(LoadingPlugin)
Vue.use(Toast)
export default {
directives: {
TransferDom
},
data(){
return {
previewerList:[],
options: {
getThumbBoundsFn: () => {

let thumbnail = document.querySelectorAll('.questions-item img')[this.imgIndex]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect();


return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}


}
},
imgIndex:0,

on: false,

translateName: "translate-left",

dataList: [],
typeArr: ["单选题","多选题","判断题","识图题","计算题"],
latterArr: ["A","B","C","D","E","F"],

isExplain: false,

currentData: {},

currentIndex: 0,

checked: "",

pageSize: 20,
pageNow: 1,

count: 0,

isAnswer: "answer_no",

startX: 0,
moveX: 0,
disX: 0,
isLoading: true,
ids: [],
isempty: false

}
},

components:{
Answercard,
Previewer,
Loading,
Icon
},

created(){
this.getQuery();
console.log(this.query);
if(this.query.type) {
document.title="专项练习";
} else {
document.title="顺序练习";
}
getLearnLocation({
skillItem: this.query.skillItem,
skillLevel: this.query.skillLevel
}).then((data)=> {
console.log(data);
})
var parms = {...this.query};
this.$vux.loading.show({
text: '加载中'
});
getQuestionType(parms).then((data)=> {
this.dataList = data.data.typeList;
this.count = data.data.typeList.length;
if(this.dataList.length) {
this.jumpIndex(parseInt(parms.index));
} else {
this.isempty = true;
}
})

},

watch:{

currentIndex(val){
this.currentData = this.dataList[val];
if (this.currentData.checked) {
this.checked=this.currentData.checked;
}else{
this.checked="";
};
saveLearnLocation(this.saveLearnLocationPramas).then((data)=> {
console.log(data);
})
this.$nextTick(()=>{
var timer = setInterval(()=> {
if(this.isLoading) {
setTimeout( () => {
this.imgbindclick();
},600)
clearInterval(timer)
}
},10)

});
}
},
filters:{
answerYesClass(item){
if(item.isAnswer){
return "item item_checked"
}



return "item"
},
answerNoClass(checked,options, latter,isExplain) {
if(isExplain) {
if(options.isAnswer) {
return "item item_checked"
}
return "item"
} else {
if(checked.indexOf(latter)>=0) {
return "item item_checked"
}
return "item"
}
}

},
computed: {
...mapState(['query']),
answer() {
var answerArr = [];
if(this.currentData.type != 5) {
this.currentData.options.map((item,index)=> {
if(item.isAnswer) {

answerArr.push({
latter: this.latterArr[index],
content: item.content
})
}
})
}
return answerArr;
},

isCollect () {
if(this.currentData.favorite) {
return true;
}
return false;
},
saveLearnLocationPramas() {
var data = {
skillItem: this.query.skillItem,
skillLevel: this.query.skillLevel,
location: this.currentIndex,
};
if(this.query.type) {
data.testType = this.query.type
}
return data
},
saveAnswerPramas() {
var data= {
skillItem: this.query.skillItem,
skillLevel: this.query.skillLevel,
testId: this.currentData.id,

}
if(this.query.type) {
data.testType = this.query.type
}
if(this.currentData != undefined) {
data.result = this.currentData.result?1:0
}
return data
}
},
mounted() {
this.$nextTick(()=>{
var timer = setInterval(()=> {
if(this.isLoading) {
setTimeout(()=> {
this.imgbindclick();
},600);
clearInterval(timer)
}
},10)
})
},

methods: {
splitIds(operation,index,size) {
if(!size) {
size = 20;
}
var beginIndex = 0;
var endIndex = 0;
if(operation == "pre") {
if(index >= size) {
beginIndex = index - size;
}
if(index > 0) {
endIndex = index;
}
} else {
beginIndex = index;
endIndex = beginIndex + size;
if(endIndex >= this.count) {
endIndex = this.count;
}
}
var ids = [];
for(var i = beginIndex; i < endIndex; i++) {
ids.push(this.dataList[i].id);
}
return ids;
},
getData() {
var parms = {...this.query};
parms.ids =  this.ids
getQuestion(parms).then((data)=> {
this.dataList = this.appendData(this.dataList,data.data.testList);
this.currentData = this.dataList[this.currentIndex];
this.isLoading = true;
})
},
saveHistory(params) {
saveHistory(params).then((data)=> {
console.log(data);
})
},
appendData(data,append) {
return data.map((item)=> {
var elt = append.find((it)=> {
return it.id == item.id
})
if(elt) {
Object.assign(item, elt);
}
return item;
});
},
...mapMutations(['getQuery']),

tabChange(option) {
this.isAnswer = option;
if(option == "answer_yes") {
this.isExplain = true;
} else {
this.isExplain = false;
}
},

_touchstart(event) {
if(event.touches.length == 1) {

this.startX = event.touches[0].clientX;
}

},
_touchmove(event) {
if(event.touches.length == 1) {

this.moveX = event.touches[0].clientX;

this.disX = this.startX - this.moveX;
}
},
_touchend(event) {
if(event.changedTouches.length == 1) {
if(this.disX>0 && this.disX>50) {
this.nextEvent();
}
if(this.disX<0 && this.disX<-50) {
this.preEvent();
}
}
this.disX = 0;
},

isExplainEvent(){
if(this.isAnswer == "answer_yes") {
return
}
this.isExplain=!this.isExplain
},


response(letter,options){
if(this.isExplain) {
return
}
if (this.currentData.type == 2 ) {
if (this.checked.indexOf(letter)>=0) {
this.checked = this.checked.replace(letter,"");
}else{
this.checked += letter;
}
this.checked = this.checked.split("").sort().join("");
this.$set(this.currentData,'checked',this.checked);
if(this.checked == this.multiAnswer()) {
this.$set(this.currentData,'result',1);
} else {
this.$set(this.currentData,'result',0);
}
}else{
this.$set(this.currentData,'checked',letter)
this.checked = letter;
if(options.isAnswer) {
this.$set(this.currentData,'result',1)
} else {
this.$set(this.currentData,'result',0)
}

}
saveAnswerHistory(this.saveAnswerPramas).then((data)=> {
console.log(data);
})
},
multiAnswer() {
var latter = "";
this.answer.forEach((item,index)=> {
latter += item.latter
})
return latter
},

preEvent() {
if(this.currentIndex<=0){

this.$vux.toast.show({
type: 'text',
text: '已经是第一道题',
time: 1000,
position: 'middle'
});
return;
};
if(!this.isLoading) {
this.$vux.loading.show({
text: '加载中'
});
var timer = setInterval(()=> {
if(this.isLoading) {
clearInterval(timer);
this.$vux.loading.hide();
this.currentIndex--;
}
}, 10)
} else {
this.currentIndex--;
}




if(this.isAnswer == "answer_no") {
this.isExplain = false;
} else {
this.isExplain = true;
}
this.translateName = "translate-right";
this.on = !this.on;
if(this.currentIndex - 1 > 0 && !this.dataList[this.currentIndex-1].content) {
this.ids = this.splitIds("pre", this.currentIndex);
this.isLoading = false;
this.getData();
}
},

nextEvent() {

if(this.currentIndex>=this.count-1){

this.$vux.toast.show({
type: 'text',
text: '已经是最后一道题',
time: 1000,
position: 'middle'
});
return;
}
if(!this.isLoading) {
this.$vux.loading.show({
text: '加载中'
});
var timer = setInterval(()=> {
if(this.isLoading) {
clearInterval(timer);
this.$vux.loading.hide();
this.currentIndex++;
}
}, 10)
} else {
this.currentIndex++;
}




if(this.isAnswer == "answer_no") {
this.isExplain = false;
} else {
this.isExplain = true;
}

this.translateName = "translate-left"
this.on = !this.on;
if(this.currentIndex + 1 < this.count &&  !this.dataList[this.currentIndex+1].content) {
this.ids = this.splitIds("next",this.currentIndex+1);
this.isLoading = false;
this.getData();
}
},

showCard(){
this.$refs.answercardRef.open();
if(!this.isLoading) {
this.$vux.loading.show({
text: '加载中'
});
var timer = setInterval(()=> {
if(this.isLoading) {
clearInterval(timer);
this.$vux.loading.hide();
}
}, 10)
}
},

jumpIndex(index){
this.currentIndex = index;
var toLoading = false;
var beginIndex = parseInt(this.currentIndex/20)*20;
this.ids = [];
if(!this.dataList[this.currentIndex].content) {
this.ids.push(...this.splitIds("next", beginIndex));
toLoading = true;
}
if(this.currentIndex == beginIndex + 19 && this.currentIndex + 1 < this.count && !this.dataList[this.currentIndex + 1].content) {
this.ids.push(...this.splitIds("next", this.currentIndex+1));
toLoading = true;
}
if(this.currentIndex == beginIndex && this.currentIndex - 1 > 0 && !this.dataList[this.currentIndex - 1].content) {
this.ids.push(...this.splitIds("pre", this.currentIndex));
toLoading = true;
}
if(toLoading) {
this.isLoading = false;
this.getData();
if(!this.isLoading) {
this.$vux.loading.show({
text: '加载中'
});
var timer = setInterval(()=> {
if(this.isLoading) {
clearInterval(timer);
this.$vux.loading.hide();
}
}, 10)
}
};
if(this.isAnswer == "answer_no") {
this.isExplain = false;
} else {
this.isExplain = true;
}
},

imgbindclick(){
var _this = this
var imgWrap = document.querySelectorAll(".questions-item")[0]||document.querySelectorAll(".questions-item")[1];
var imgs = imgWrap.querySelectorAll("img")
if(imgs.length) {
for (var i = 0; i < imgs.length; i++) {
;(function(i) {
imgs[i].onclick=function(event){
event.stopPropagation();
var p = imgs[i].clientWidth/imgs[i].clientHeight
var h = 700/p
_this.previewerList= [(
{
src: this.src,
h: h,
w: 700,
}
)];
_this.imgIndex = i;
setTimeout( () => {
_this.$refs.previewer.show(0)
})

}
})(i)
}
}
},
collectEvent() {
if(this.isCollect) {
delFavorite({
testId: this.currentData.id
}).then((data)=> {
if(data.data.success) {
this.$set(this.currentData,"favorite",false)
this.$vux.toast.show({
text: '取消成功',
type: "success",
time: 1000
})
}

})
} else {
favorite({
testId: this.currentData.id
}).then((data)=> {
if(data.data.success) {
this.$set(this.currentData,"favorite",true)
this.$vux.toast.show({
text: '收藏成功',
type: "success",
time: 1000
})
}
})
}
}
}
}



